___CSS_LOADER_EXPORT___.push([module.id, `/* HeroSection.css */
.hero-section {
    position: relative;
    width: 100%;
    height: 100vh;
    display: flex;
    justify-content: center;
    align-items: center;
    overflow: hidden;
    background-color: #f0f0f0;
  }
  
  .hero-section img {
    position: absolute;
    top: 0;
    left: 0;
    width: 100%;
    height: 100%;
    object-fit: cover;
    z-index: -1;
  }
  
  .hero-content {
    position: relative;
    text-align: center;
    color: white;
    font-family: Arial, sans-serif;
  }
  
  .company-name {
    font-size: 3rem;
    font-weight: bold;
    margin-bottom: 20px;
    letter-spacing: 2px;
  }
  
  .line {
    position: absolute;
    width: 80px;
    height: 4px;
    background-color: white;
    top: 50%;
  }
  
  .line.left {
    left: -100px;
  }
  
  .line.right {
    right: -100px;
  }
  
  @media (max-width: 768px) {
    .hero-section img {
      content: url('https://i.imgur.com/yourHeroImageMobile.jpeg');
    }
    
    .company-name {
      font-size: 2rem;
    }
  }
